<template>
  <div>
    <section class="container mt-4">
      <swiper :options="swiperOption" ref="mySwiper" class="container p-0">
        <swiper-slide>
          <img class="img-fluid" src="../../static/images/content/Healthfood.png" alt title>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>
        <!-- <div class="swiper-scrollbar" slot="scrollbar"></div> -->
      </swiper>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
import "../../../node_modules/swiper/dist/css/swiper.css";

import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  name: "home",
  metaInfo: {
    meta: [
      {
        property: "og:title",
        content: "Greenvalley"
      },
      { property: "og:description", content: "Test 章魚" },
      { property: "og:type", content: "Test website" },
      { property: "og:url", content: "localhost" },
      {
        property: "og:image",
        content: "https://upload.cc/i1/2018/09/02/1AvCdt.png"
      }
    ],
    title: "Greenvalley",
  },
  data() {
    return {
      swiperOption: {
        // NotNextTick is a component's own property, and if notNextTick is set to true, the component will not instantiate the swiper through NextTick, which means you can get the swiper object the first time (if you need to use the get swiper object to do what Things, then this property must be true)
        // notNextTick是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true
        autoplay: false, //是否自动播放，默认true
        loop: true,
        notNextTick: true,
        // swiper configs 所有的配置同swiper官方api配置
        // direction : 'vertical',
        grabCursor: true,
        setWrapperSize: true,
        // autoHeight: true,
        paginationType: "progressbar",
        pagination: ".swiper-pagination",
        paginationClickable: true,
        // scrollbar:'.swiper-scrollbar',
        mousewheelControl: true,
        observeParents: true,
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        }
      }
    };
  },
  components: {
    swiper,
    swiperSlide
  },
  // you can find current swiper instance object like this, while the notNextTick property value must be true
  // 如果你需要得到当前的swiper对象来做一些事情，你可以像下面这样定义一个方法属性来获取当前的swiper对象，同时notNextTick必须为true
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  }
};
</script>
